import React, {useState, useCallback, useEffect} from 'react';
/* * useCallback会将我们传递给它的函数fnB返回，并且将这个结果缓存；当依赖a变更时，会返回新的函数。既然返回的是函数，我们无法很好的判断返回的函数是否变更，所以我们可以借助ES6新增的数据类型Set来判断，具体如下： * */
const set = new Set();

export function UseCallback() {
    const [count, setCount] = useState(1);
    const [val, setVal] = useState('');
    const callback = useCallback(() => {
        if (count % 2 === 0) {
            console.log("偶数", count);
        }
    }, [count]);
    set.add(callback);
    return <div><p>通过set记录，useCallback的更新记录</p>    <h4>{count}</h4>    <h4>{set.size}</h4>
        <div>
            <button onClick={() => setCount(count + 1)}>+</button>
            <input value={val} onChange={event => setVal(event.target.value)}/></div>
    </div>;
}

export function Parent() {
    const [count, setCount] = useState(1);
    const [val, setVal] = useState('');
    const callback = useCallback(() => {
        return count;
    }, [count]);
    return <div><p>利用useCallback的缓存函数</p>
        <h4>{count}</h4>
        <Child callback={callback}/>
        <div>
            <button onClick={() => setCount(count + 1)}>+</button>
            <input value={val} onChange={event => setVal(event.target.value)}/></div>
    </div>;
}

function Child({callback}) {
    const [count, setCount] = useState(() => callback());
    useEffect(() => {
        setCount(callback());
    }, [callback]);
    return <div>    {count}   </div>
}