import React, {useMemo, useState} from 'react';
import {WhiteSpace, Button, Picker} from 'antd-mobile';

const PlaceHolder = ({className = '', ...restProps}) => (
    <div className={`${className} placeholder`} {...restProps}>{restProps.children}</div>);
export default (props) => {
    const [visible, setVisible] = useState(false);
    const [value, setValue] = useState(['Callback']);
    const compList = useMemo(() => {
        return [{
            label: '同源--父调用子页面', value: '1', fn: () => {
                const oIframe = document.getElementById("HookDemo_iframe1");
                const oIframeWindow = oIframe.contentWindow;
                oIframeWindow.document.getElementsByClassName("myContent")[0].innerHTML = "changed by Parenthtml"
                console.log("iframeStore", oIframe.contentWindow.iframeStore);
            }
        },]
    }, [])
    const PickerChangeFn = (val) => {
        const filterItem = compList.filter(item => item.value === val[0])[0]
        if (filterItem) {
            filterItem.fn()
        }
        setValue(val);
    }
    return <div><PlaceHolder className="inline Homologous_iframe1">同源--iframe1</PlaceHolder>
        <iframe name={"HookDemo_iframe1"} id={"HookDemo_iframe1"}
                src={"https://jeonwei.space/static/domainPages/CrossDomainOrNot/HomologousDemo_child1.html"}></iframe>
        <Button onClick={() => setVisible(true)}>control</Button> <Picker cols={1} visible={visible} data={compList}
                                                                          value={value} onChange={PickerChangeFn}
                                                                          onOk={() => setVisible(false)}
                                                                          onDismiss={() => setVisible(false)}/>
        <hr/>
        <WhiteSpace/> <PlaceHolder className="inline Homologous_iframe1">同源--iframe2</PlaceHolder>
        <iframe name={"HookDemo_ifame2"} id={"HookDemo_ifame2"}
                src={"https://jeonwei.space/static/domainPages/CrossDomainOrNot/HomologousDemo_child2.html"}></iframe>
    </div>
}