import React from 'react'
import {Button} from 'antd-mobile';
const index = (props) => {
    return <div>
        <Button onClick={() => {
            sessionStorage.setItem("userInfo", JSON.stringify({id: 101}));
            const lastUrlLocation = JSON.parse(sessionStorage.getItem("lastUrlLocation"));
            console.log(lastUrlLocation)
            sessionStorage.removeItem("lastUrlLocation")
            if (lastUrlLocation.pathname === "/") {
                props.history.push("Home")
            } else if (lastUrlLocation.pathname === "/Login") {
                props.history.push("Home")
            } else if (lastUrlLocation) {
                props.history.push(lastUrlLocation)
            }
        }}>登录
        </Button>
    </div>
}
export default index