import {useMemo, useState} from "react";
import {Parent, UseCallback} from "./useCallback";
import {MemoryComp, WithMemo, WithoutMemo} from "./useMemo";
import UseContextUseReducer from "./useContext+useReducer";
import {App as UseRef1, BetterMessageThread as UseRef3, MessageThread as UseRef2} from "./useRef";
import {App as UseImperativeHandleApp} from "./useImperativeHandle";
import {App as UseLayoutEffect} from "./useLayoutEffect";
import {Card, WhiteSpace, Picker, List,Button} from 'antd-mobile';
import React from "react";

const HookDemo = (props) => {
    const [visible, setVisible] = useState(false);
    const [value, setValue] = useState(['Callback']);
    const compList = useMemo(() => {
        return [{
            label: 'Callback 用法',
            value: 'Callback',
            content: <div><UseCallback/>
                <hr/>
                <Parent/></div>
        }, {
            label: 'Memo 用法',
            value: 'Memo',
            content: <div> WithoutMemo <WithoutMemo/>
                <hr/>
                WithMemo <WithMemo/>
                <hr/>
                MemoryComp <WhiteSpace size="lg"/> <MemoryComp/></div>
        }, {
            label: 'useContext + useReducer 用法',
            value: 'useContext + useReducer',
            content: <div> useContext + useReducer <UseContextUseReducer/></div>
        }, {
            label: 'useRef 用法',
            value: 'useRef',
            content: <div><UseRef1/>
                <hr/>
                <WhiteSpace size="lg"/> <UseRef2/>
                <hr/>
                <WhiteSpace size="lg"/> <UseRef3/></div>
        }, {
            label: 'useImperativeHandle 用法',
            value: 'useImperativeHandle',
            content: <div><UseImperativeHandleApp/></div>
        }, {label: 'useLayoutEffect 用法', value: 'useLayoutEffect', content: <div><UseLayoutEffect/></div>}]
    }, [])
    const itemData = useMemo(() => {
        return compList.filter((item) => {
            return value[0] === item.value
        })[0]
    }, [value])
    return <>
        <Picker cols={1} visible={visible} data={compList} value={value} onChange={v => {
            console.log(v);
            setValue(v)
        }} onOk={() => setVisible(false)} onDismiss={() => setVisible(false)}>
            <List.Item extra
                       onClick={() => setVisible(true)}>
                Visible state
            </List.Item>
        </Picker>
        <WhiteSpace size="lg"/> <Card>
        <Card.Header title={itemData.label}/>
        <Card.Body>
            {itemData.content}
        </Card.Body>
    </Card>
    </>
}
export default HookDemo