import React, {useEffect, useState,useRef} from "react";  /* 案例一  */
import {List, Switch ,Button, InputItem, Card} from 'antd-mobile';

export default function Index(props) {
    const {match: {params}} = props;
    const [checked, setChecked] = useState(true);
    const [inputContext, setInputContext] = useState("123");

    let CompClass1Ref = useRef(null);
    console.log("刷新")
    const commitFn = () =>{
        console.log("CompClass1",CompClass1Ref.current());
        console.log("CompClass2",{checked,inputContext});
    }

    return <div>


            <Card.Header
                title={`产品 ${params.id} page!!`}
            />
            <hr/>
            <CompClass1 initState={{checked:true,inputContext:"123"}} CompClass1Ref={CompClass1Ref}/>
            <CompClass2 checked={checked} setChecked={setChecked} inputContext={inputContext} setInputContext={setInputContext}/>
        <Button type={"primary"} onClick={commitFn}>提交</Button>
    </div>
}


const CompClass1 = ({initState,CompClass1Ref}) =>{
    /* 这个默认值是对界面的交互优化，防止二次渲染，闪屏，如果当组件首次加载时，会执行这里的默认值，然后再走didmount */
    const [checked, setChecked] = useState(initState.checked);
    const [inputContext, setInputContext] = useState(initState.inputContext);

    const upData = (data)=>{
        CompClass1Ref.current = () =>{
            return {
                checked,
                inputContext,
                ...data
            }
        }
    }

    useEffect(()=>{
        setChecked(initState.checked);
        setInputContext(initState.inputContext)
        upData({checked:initState.checked,inputContext:initState.inputContext})
    },[])

    return  <List
        renderHeader={() => '内控状态组件'}
    >
        <List.Item
            extra={<Switch
                checked={checked}
                onChange={() => {
                    setChecked(!checked);
                    upData({checked:!checked})
                }}
            />}
        >Off</List.Item>
        <InputItem placeholder={"请输入"} value={inputContext} onChange={val=>{
            setInputContext(val);
            upData({inputContext:val});
        }}/>
    </List>
}

const CompClass2 = ({checked,setChecked,inputContext,setInputContext}) =>{

    return  <List
        renderHeader={() => '外控状态组件'}
    >
        <List.Item
            extra={<Switch
                checked={checked}
                onChange={() => setChecked(!checked)}
            />}
        >Off</List.Item>
        <InputItem placeholder={"请输入"} value={inputContext} onChange={(value)=>setInputContext(value)}/>
    </List>
}