import React, {useState, useEffect} from 'react';

const useBindHook = (initialValue) => {
    const [value, setValue] = useState(initialValue || "");
    const onChange = val => {
        setValue(val)
    };
    return {value, onChange};
}
const Index = (props) => {
    const {value, onChange} = useBindHook("");
    const click = () => {
        onChange(!value)
    }
    console.log(value)
    return <div>      {value ? <div onClick={click}>show</div> : <div onClick={click}>unShow</div>}
        <hr/>
        <div style={{width: window.innerWidth, height: window.innerHeight * 0.5, background: "#ddd"}}>
            {value ?
            <div style={{width: "100%", height: "100%", background: "#561199"}}>1--more ...</div> :
            <div style={{width: "100%", height: "100%", background: "#667711"}}>2--more ...</div>}      </div>
    </div>
}
export default Index;