import React, {useState, useRef} from "react";

/*useRef 返回的值传递给组件或者 DOM 的 ref 属性，就可以通过 ref.current 值访问组件或真实的 DOM 节点，从而可以对 DOM 进行一些操作，比如监听事件等等。
当然 useRef 远比你想象中的功能更加强大，useRef 的功能有点像类属性，或者说您想要在组件中记录一些值，并且这些值在稍后可以更改。
利用 useRef 就可以绕过 Capture Value 的特性。可以认为 ref 在所有 Render 过程中保持着唯一引用，因此所有对 ref 的赋值或取值，拿到的都只有一个最终状态，而不会在每个 Render 间存在隔离。*/

export function App() {
    let [name, setName] = useState("Nate");
    let nameRef = useRef();
    const submitButton = () => {
        setName(nameRef.current.value);
    };
    return (<div> 通过 ref.current 值访问组件或真实的 DOM 节点 <p>{name}</p>
        <div><input ref={nameRef} type="text"/>
            <button type="button" onClick={submitButton}> Submit</button>
        </div>
    </div>);
}

/*在点击 Send 按钮后，再次修改输入框的值，3 秒后的输出依然是点击前输入框的值。这就是所谓的 capture value 的特性。
而在类组件中 3 秒后输出的就是修改后的值，因为这时候 message 是挂载在 this 变量上，它保留的是一个引用值，对 this 属性的访问都会获取到最新的值。
讲到这里你应该就明白了，useRef 创建一个引用，就可以有效规避 React Hooks 中 Capture Value 特性。*/

export function MessageThread() {
    const [message, setMessage] = useState("");
    const showMessage = () => {
        alert("You said: " + message);
    };
    const handleSendClick = () => {
        setTimeout(showMessage, 3000);
    };
    const handleMessageChange = e => {
        setMessage(e.target.value);
    };
    return (<div><p> Capture Value 的特性 </p>        <input value={message} onChange={handleMessageChange}/>
        <button onClick={handleSendClick}>Send</button>
    </div>);
}

export function BetterMessageThread() {
    const latestMessage = useRef("");
    const showMessage = () => {
        alert("You said: " + latestMessage.current);
    };
    const handleSendClick = () => {
        setTimeout(showMessage, 3000);
    };    /*只要将赋值与取值的对象变成 useRef，而不是 useState，就可以躲过 capture value 特性，在 3 秒后得到最新的值。*/
    const handleMessageChange = e => {
        latestMessage.current = e.target.value;
    };
    return (<div><p> 绕过 Capture Value 的特性 </p>        <input onChange={handleMessageChange}/>
        <button onClick={handleSendClick}>Send</button>
    </div>);
}