import React, {useMemo, useState} from "react";  /* 案例一  */
export function WithoutMemo() {
    const [count, setCount] = useState(1);
    const [val, setValue] = useState('');

    function expensive() {
        let sum = 0;
        for (let i = 0; i < count * 100; i++) {
            sum += i;
        }
        return sum;
    }

    return <div><h4>{count}-{val}-{expensive()}</h4>
        <div>
            <button onClick={() => setCount(count + 1)}>+c1</button>
            <input value={val} onChange={event => setValue(event.target.value)}/></div>
    </div>;
}  /* 案例二  */
export function WithMemo() {
    const [count, setCount] = useState(1);
    const [val, setValue] = useState('');
    const expensive = useMemo(() => {
        let sum = 0;
        for (let i = 0; i < count * 100; i++) {
            sum += i;
        }
        return sum;
    }, [count]);
    return <div><h4>{count}-{val}-{expensive}</h4>
        <div>
            <button onClick={() => setCount(count + 1)}>+c1</button>
            <input value={val} onChange={event => setValue(event.target.value)}/></div>
    </div>;
}  /* 案例三  记忆组件  */
export function MemoryComp() {
    const [a, setA] = useState(1);
    const [b, setB] = useState(1);
    const [c, setC] = useState(1);   /* Only re-rendered if `a` changes: */
    const child1 = useMemo(() => {
        console.log("run MemoA")
        return <Child1 a={a}/>
    }, [a]);   /* Only re-rendered if `b` changes: */
    const child2 = useMemo(() => {
        console.log("run MemoB")
        return <Child2 b={b}/>
    }, [b]);
    return (<div>
        <button onClick={() => setA(a + 1)}>+A</button>
        | <button onClick={() => setB(b + 1)}>+B</button>| <button
        onClick={() => setC(c + 1)}>+C</button>        {child1} {child2}
        <div>C:{c}</div>
        <Child3/></div>)
}

function Child1({a}) {
    console.log("run ChildA")
    return <div>A:{a}</div>
}

function Child2({b}) {
    console.log("run ChildB")
    return <div>B:{b}</div>
}

function Child3() {
    console.log("run ChildD")
    return <div>ChildD</div>
}