import React, {useContext, createContext, useReducer} from "react";

const TestDispatch = createContext(null);

function UseReduce() {
    const [state, dispatch] = useReducer((state, action) => {
        return {...state, ...action};
    }, {num: 1, show: true});
    return (<TestDispatch.Provider value={{dispatch, state}}><p>parent</p>
        <hr/>
        <a style={{display: state.show ? 'block' : 'none'}}>link{state.num}</a>
        <hr/>
        <Son num={state.num} show={state.show}></Son> </TestDispatch.Provider>)
};

function Son(props) {
    const {num, show} = props;
    const {dispatch} = useContext(TestDispatch);
    return (<div><p>Son</p>
        <button onClick={() => dispatch({show: !show})}>show or hide</button>
        <button onClick={() => dispatch({num: num + 1})}>change num</button>
        <Grandson/></div>)
};

function Grandson(props) {
    const {state} = useContext(TestDispatch);
    return (<div><p>Grandson</p> Reduce store is : {JSON.stringify(state)}      </div>)
};
export default UseReduce;