import React, {useMemo, useState} from 'react';
import {WhiteSpace, Button, Picker} from 'antd-mobile';

const PlaceHolder = ({className = '', ...restProps}) => (
    <div className={`${className} placeholder`} {...restProps}>{restProps.children}</div>);
export default (props) => {
    const [visible, setVisible] = useState(false);
    const [value, setValue] = useState(['Callback']);
    const compList = useMemo(() => {
        return [{
            label: '非同源--父调用子页面', value: '1', fn: () => {
            }
        },]
    }, [])
    const PickerChangeFn = (val) => {
        const filterItem = compList.filter(item => item.value === val[0])[0]
        if (filterItem) {
            filterItem.fn()
        }
        setValue(val);
    }
    return <div>
        <PlaceHolder className="inline Homologous_iframe1">非同源--postMessage--iframe1</PlaceHolder>
        <iframe name={"iframeDemo_iframe1"} id={"iframeDemo_iframe1"}
                src={"https://jeonwei.space/static/domainPages/CrossDomainOrNot/Non-HomologousDemo-hash-passmassege-a.html"}></iframe>
        <hr/>
        <PlaceHolder className="inline Homologous_iframe2">非同源--hash--iframe2</PlaceHolder>
        <iframe name={"iframeDemo_iframe2"} id={"iframeDemo_iframe2"}
                src={"https://jeonwei.space/static/domainPages/CrossDomainOrNot/Non-HomologousDemo-hash-a.html"}></iframe>
        <hr/>
        <PlaceHolder className="inline Homologous_iframe3">非同源--name--iframe3</PlaceHolder>
        <iframe name={"iframeDemo_iframe3"} id={"iframeDemo_iframe3"}
                src={"https://jeonwei.space/static/domainPages/CrossDomainOrNot/Non-HomologousDemo-name-a.html"}></iframe>
        <Button onClick={() => setVisible(true)}>control</Button>
        <Picker cols={1} visible={visible} data={compList}
                value={value} onChange={PickerChangeFn}
                onOk={() => setVisible(false)}
                onDismiss={() => setVisible(false)}/>
    </div>
}