import React from 'react'
import {Switch, Route, Link} from 'react-router-dom';
import CompDemo from './demo/productPages/productComp/compDemo';
import {IframeHomologousDemo, NonHomologousDemo, HookDemo} from './demo/productPages';

const Index = (props) => {
    const {match} = props;
    return <div>
        <div><Link style={{margin: "0 12px"}} to={`${match.path}/1`}>产品1---跨域--同源</Link></div>
        <div><Link style={{margin: "0 12px"}} to={`${match.path}/2`}>产品2---跨域--非同源</Link></div>
        <div><Link style={{margin: "0 12px"}} to={`${match.path}/3`}>产品3---Hook用法</Link></div>
        <div><Link style={{margin: "0 12px"}} to={`${match.path}/4`}>产品4--component--内外控组件</Link></div>
        <hr/>
        <Switch>
            <Route path={`${match.path}/:id`} component={(props) => {
                const {match: {params}} = props;
                if (params.id === "1") {
                    return <IframeHomologousDemo {...props}/>
                }else if (params.id === "2") {
                    return <NonHomologousDemo {...props}/>
                }else if (params.id === "3") {
                    return <HookDemo {...props}/>
                } else if (params.id === "4") {
                    return <CompDemo {...props}/>
                }else {
                    return <div>more ... </div>
                }

            }}/>
        </Switch>
    </div>
}
export default Index