import React from 'react'
import {Switch, Route, Link, Redirect} from 'react-router-dom'
import About from "./about";
import Product from "./product";
import HomePage from "./HomePage";
import {Tabs, WhiteSpace} from 'antd-mobile';

const linkStyle = {display: 'inline-block', width: '100%', textAlign: 'center'}
const Main = (props) => {
    const {match,location} = props;
    const tabs = [{title: <Link style={linkStyle} to={`/Home`}>主页</Link>, sub: '1'}, {
        title: <Link style={linkStyle} to={`${match.path}/about`}>关于</Link>,
        sub: '2'
    }, {title: <Link style={linkStyle} to={`${match.path}/product`}>产品</Link>, sub: '3'}, {
        title: <Link style={linkStyle} to={`/Login`} onClick={() => sessionStorage.clear()}>登出</Link>, sub: '3'
    },];


    const initialPage = [match.path,`${match.path}/about`,`${match.path}/product`].indexOf(location.pathname)

    return (<div>
        <div>
            <Tabs tabs={tabs} initialPage={initialPage} tabBarPosition="bottom" renderTab={tab => <>{tab.title}</>}/>
            <WhiteSpace/>
        </div>
        <Switch>
            <Route exact path={`${match.path}`}  component={HomePage}/>
            <Route path={`${match.path}/about`} component={About}/>
            <Route path={`${match.path}/product`} component={Product}/>
        </Switch>
    </div>)
}
export default Main