import React from 'react'
import {Switch, Route} from 'react-router-dom'
import Home from './Home'
import Login from './login'
import {BrowserRouter, Redirect} from 'react-router-dom'

const Main = (props) => {
    const checkUserFn = () => {
        const login = JSON.parse(sessionStorage.getItem("userInfo")) || {};
        if (login.id === 101) {
            return true
        }
        return false
    }
    return (<>     <BrowserRouter>
        {/* Router 过滤器*/}
    <Route render={(props) => {
        console.log("过滤。。。")         /*Login 忽略*/
        if (props.location.path == "/Login") {
            return;
        }         /*没有权限的跳到登录页*/
        if (!checkUserFn()) {             /* 记录进来的页面 */
            if (!sessionStorage.getItem("lastUrlLocation")) {
                sessionStorage.setItem("lastUrlLocation", JSON.stringify(props.location));
            }
            return <Redirect to={"/Login"}/>
        }
    }}/> <Switch>
        <Route path='/Home' component={Home}/>
        <Route exact path='/login' component={Login}/>
        <Route render={() => {
            if (checkUserFn()) {
                alert("地址有误，返回首页")
                return <Redirect to={"/Home"}/>
            }
        }}/> </Switch> </BrowserRouter>   </>)
}
export default Main